* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  margin: 0;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.header__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__nav-item {
  text-transform: uppercase;
  align-items: center;
  margin-right: 20px;
}

.header__nav-link {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.header__nav-link.is-active {
  color: #00acdc;
}

.header__nav-item:last-child {
  margin-right: 0;
}

.header__nav-link:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  width: 100%;
  padding: 50px 40px;
  display: grid;
}

@media screen and (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media screen and (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media screen and (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s ease-in-out;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: flex;
}

.card__title {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title:hover {
  color: #34568b;
}

.card__code {
  text-align: left;
  color: #616070;
  width: 100%;
  margin: 0 0 24px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 16px;
  margin: 0;
  padding-bottom: 16px;
  line-height: 14px;
  display: flex;
}

.card__reviews-p {
  color: #060b35;
  margin: 0;
  font-size: 10px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card__stars {
  flex-direction: row;
  justify-content: space-around;
  width: 96px;
  display: flex;
}

.card__stars-star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 80%;
  width: 16px;
  height: 16px;
}

.card__stars-star:nth-child(-n+1), .card__stars-star:nth-child(-n+2), .card__stars-star:nth-child(-n+3), .card__stars-star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
